@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Beth+Ellen&display=swap);
* {
  font-family: "Roboto", sans-serif;
  font-size: 1.5vw;
  color: #3d3434;
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

.borderBackground {
  background-image: -webkit-gradient(linear, left bottom, left top, from(#ff9a9e), color-stop(#ffa8a4), color-stop(#feb6ad), color-stop(#fcc3b7), to(#fad0c4));
  background-image: -webkit-linear-gradient(bottom, #ff9a9e, #ffa8a4, #feb6ad, #fcc3b7, #fad0c4);
  background-image: linear-gradient(to top, #ff9a9e, #ffa8a4, #feb6ad, #fcc3b7, #fad0c4);
  text-align: center;
  padding: 20vw; }

.transBox {
  margin: 3vw;
  background-color: rgba(255, 255, 255, 0.7);
  border: 5px dotted #ffa8a4; }
  .transBox h1 {
    font-family: 'Beth Ellen', cursive;
    margin: 5%;
    font-weight: bold;
    color: #3d3434;
    font-size: 6vw;
    border: 1px dotted blue; }

button {
  color: #fcf0ed;
  background-image: -webkit-gradient(linear, left bottom, left top, from(#ff9a9e), color-stop(#ffa8a4), color-stop(#feb6ad), color-stop(#fcc3b7), to(#fad0c4));
  background-image: -webkit-linear-gradient(bottom, #ff9a9e, #ffa8a4, #feb6ad, #fcc3b7, #fad0c4);
  background-image: linear-gradient(to top, #ff9a9e, #ffa8a4, #feb6ad, #fcc3b7, #fad0c4);
  margin-left: 1vw;
  margin-bottom: 1vw;
  border-radius: 6%;
  cursor: pointer;
  padding: 1vw 5vw;
  text-decoration: none;
  text-shadow: 0px 1px 0px #faedca; }
  button:hover {
    background-image: -webkit-gradient(linear, left top, right top, from(#ff9a9e), color-stop(#ffa8a4), color-stop(#feb6ad), color-stop(#fcc3b7), to(#fad0c4));
    background-image: -webkit-linear-gradient(left, #ff9a9e, #ffa8a4, #feb6ad, #fcc3b7, #fad0c4);
    background-image: linear-gradient(to right, #ff9a9e, #ffa8a4, #feb6ad, #fcc3b7, #fad0c4); }
  button:active {
    position: relative;
    top: 1px; }

.Tasks, .Tasks-active, .Tasks-finished {
  border: 1px solid lightgreen;
  width: 100%;
  height: 100%;
  padding: 3vw; }
  .Tasks span, .Tasks-active span, .Tasks-finished span {
    border: 1px solid yellow;
    width: 5%;
    padding: 1vw;
    top: 1vw;
    left: -1vw; }

.AddTask {
  display: flex;
  justify-content: center; }

.showButtons {
  border: 2px dotted darkblue;
  width: 100%;
  display: flex;
  justify-content: space-around;
  padding: 2vw;
  padding-left: 2.5vw;
  padding-right: 3.5vw; }
  .showButtons button {
    border: 2px dotted blue;
    width: 35%; }

.form {
  width: 50%;
  position: relative;
  height: 50px;
  overflow: hidden; }
  .form input {
    width: 100%;
    height: 100%;
    color: #3d3434;
    padding-top: 20px;
    border: none;
    outline: none;
    background: #fcf0ed; }
  .form label {
    position: absolute;
    bottom: 0px;
    left: 0%;
    width: 100%;
    height: 100%;
    pointer-events: none; }
    .form label:after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      height: 100%;
      width: 100%;
      border-bottom: 3px solid #ff9a9e;
      -webkit-transform: translateX(-100%);
              transform: translateX(-100%);
      -webkit-transition: -webkit-transform 0.3s ease;
      transition: -webkit-transform 0.3s ease;
      transition: transform 0.3s ease;
      transition: transform 0.3s ease, -webkit-transform 0.3s ease; }
    .form label .contentName {
      position: absolute;
      bottom: 5px;
      left: 0;
      -webkit-transition: all 0.3s ease;
      transition: all 0.3s ease; }

.form input:focus + .labelName .contentName,
.form input:valid + .labelName .contentName {
  -webkit-transform: translateY(-150%);
          transform: translateY(-150%);
  font-size: 14px;
  color: #3d3434; }

.form input:focus + .labelName::after,
.form input:valid + .labelName::after {
  -webkit-transform: translateX(0%);
          transform: translateX(0%); }

.borderTasks {
  background-color: rgba(255, 255, 255, 0.1);
  padding-top: 1vw;
  border: 1px solid red; }
  .borderTasks button {
    width: 10%;
    justify-content: center; }
    @media (max-width: 881px) {
      .borderTasks button {
        justify-content: flex-start; } }
    .borderTasks button .material-icons {
      color: #fcf0ed;
      justify-content: center;
      margin-left: -1.5vw; }

.introBox {
  display: flex;
  justify-content: center;
  margin-top: 25%; }
  .introBox .showIntroButton {
    width: 25%;
    top: 50%;
    down: 50%; }

